import Vue from "vue"
import Vuex from "vuex"
import VuePersist from 'vuex-persist'
const vuexLocal = new VuePersist({
    storage:window.localStorage
})
Vue.use(Vuex)
export default new Vuex.Store({
    plugins:[vuexLocal.plugin],
    state:{
        users:[],
        shop:[],
    },
    mutations: {
        add(state,t){
            // state.users=[]
            console.log(t)
            state.users.push(t)
        },
        addimg(state,t){
            state.users[0].avatar=t
        },
        aa(state){
            state.users.splice(0,1)
        },
        jia(state,t){

            var r = state.shop.findIndex((item,index)=>{
                return item.id==t.id
            })
            if(r<0){
                state.shop.push(t)
            }else {
                state.shop[r].num++
            }
            // console.log(r,t)
        },
        jian(state,t){
            var r = state.shop.findIndex((item,index)=>{
                return item.id==t.id
            })
            if(r<0){
                
            }else {
                state.shop[r].num--
                if(state.shop[r].num<1){
                    state.shop.splice(r,1)
                }
            }
        },
        cartjia(state,t){
            state.shop[t].num++
        },
        cartjian(state,t){
            state.shop[t].num--
            if(state.shop[t].num<1){
                state.shop.splice(t,1)
            }
        }
    },
    getters:{
        nums(state){
            var r = 0;
           state.shop.map((item)=>{
               r+=item.num*item.pirce
           })
            return r
        },
        Num(state){
            var r = 0;
            state.shop.map((item)=>{
                r+=item.num
            })
            return r
        }
    }
})


