import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
const City = () => import("./../page/city/City") 
const Home = () => import('./../page/home/Home')
const Scity = () => import('./../page/smallcity/Scity')
const Search = () => import('./../page/home/Search')
const Order = () => import('./../page/home/Order')
const User = () => import('./../page/home/User')
const Register = () => import('./../page/register/Register')
const Registerok = () => import('./../page/register/Registerok')
const Resetpass = () => import('./../page/register/Resetpass')
const Shopadd = () => import('./../page/address/Shopadd')
const Message = () => import('./../page/address/Message')
const Seep = () => import('./../page/address/Seep')
const Details = () => import('./../page/home/Details')
const AAA = () => import('./../page/aaa')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: "/City",
    },
    {
      path: '/AAA',
      name: 'AAA',
      component: AAA,
    },
    {
      path: '/City',
      name: "City",
      component: City,
    },
    {
      path: '/Home',
      name: "Home",
      component: Home,
    },
    {
      path: '/Scity/:id/:name',
      name: "Scity",
      component: Scity
    },
    {
      path: '/Search',
      name: "Search",
      component: Search,
    },
    {
      path: '/Order',
      name: "Order",
      component: Order,
    },
    {
      path: '/User',
      name: "User",
      component: User,
    },
    {
      path: '/Register',
      name: "Register",
      component: Register,
    },
    {
      path: '/Registerok',
      name: "Registerok",
      component: Registerok,
    },
    {
      path: '/Resetpass',
      name: "Resetpass",
      component: Resetpass,
    },
    {
      path: '/Shopadd',
      name: "Shopadd",
      component: Shopadd,
    },
    {
      path: '/Message',
      name: "Message",
      component: Message,
      meta:{keepAlive:true}
    },
    {
      path: '/Seep',
      name: "Seep",
      component: Seep,
    },
    {
      path: '/Details',
      name: "Details",
      component: Details,
    }
  ]
})



